.wrapper {
  min-height: 100vh;
  overflow: hidden;
  padding: 0 10rem;
  padding-top: 5rem;
}

.contact {
  padding: 2rem 0;
}
.contact_ftom_contaner form {
  margin: 2rem 0;
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 2rem;
}
.contact_ftom_contaner form :is(input, textarea) {
  min-height: 2rem;
  background: none;
  padding: 1rem;
  border: 2px solid #484848;
}
.contact_ftom_contaner form input:nth-child(4) {
  height: 20rem;
}
.contact_ftom_contaner form input:last-child {
  width: 15rem;
  color: var(--frnt-clr);
  align-self: flex-end;
  background: #2bec6c;
  cursor: pointer;
}
